.summary {
  text-align: center;
  max-width: 45rem;
  width: 90%;
  margin: auto;
  margin-top: 8rem;
  position: relative;
  background-color: #383838;
  color: white;
  border-radius: 14px;
  padding: 1rem;
  box-shadow: 0 1px 18px 10px rgba(0, 0, 0, 0.25);
}

.summary h2 {
  font-size: 2rem;
  font-weight: normal;
  margin-top: 0;
}

@media (max-width: 1000px) {
  .summary h2 {
    font-size: 1.5rem;
  }
  .summary p {
    color: #ddd;
  }
}